<template>
  <v-card class="mt-6 mb-10 mx-auto" max-width="1200">
    <v-card-title>
      <v-btn
        outlined
        class="mr-4"
        color="grey darken-2"
        @click="setToday"
      >
        Today
      </v-btn>

      <v-btn
        fab
        text
        small
        color="grey darken-2"
        @click="prev"
      >
        <v-icon small>
          mdi-chevron-left
        </v-icon>
      </v-btn>

      <v-btn
        fab
        text
        small
        color="grey darken-2"
        @click="next"
      >
        <v-icon small>
          mdi-chevron-right
        </v-icon>
      </v-btn>

      <span v-if="$refs.calendar">Vaccination Team Schedules ({{ $refs.calendar.title }})</span>
    </v-card-title>

    <v-card-text>
      <v-alert
        v-model="alert.isOpen"
        border="left"
        :type="alert.type"
        :color="alert.color"
        dismissible
      >
        {{alert.message}}
      </v-alert>

      <v-calendar
        ref="calendar"
        type="month"
        v-model="focus"
        color="primary"
        :events="events"
      ></v-calendar>
    </v-card-text>
  </v-card>
</template>

<script>
import axios from 'axios'

export default {
  name: 'UserTeamSchedulesCalendar',

  data: () => ({
    events: [],
    focus: '',
    alert: {
      isOpen: false,
      type: 'warning',
      message: 'Something went wrong. Please contact your IT.',
      color: 'warning',
    },
    isLoading: true,
    colors: [
      'green',
      'blue',
      'magenta',
    ]
  }),

  created () {
    this.getEvents()
  },

  mounted () {
    this.$refs.calendar.move(0)
  },

  methods: {
    openAddDialog () {
      this.$emit('openAdd')
    },

    setToday () {
      this.focus = ''
    },

    prev() {
      this.$refs.calendar.prev()
    },

    next() {
      this.$refs.calendar.next()
    },

    getColor(index) {
      const safeIdx = index % this.colors.length

      return this.colors[safeIdx]
    },

    async getEvents() {
      this.isLoadingItems = true

      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const {data} = await axios.get(`${apiHost}/schedules`, config)

        this.events = data.map((schedule, i) => {
          return {
            name: schedule.location,
            start: new Date(schedule.schedule),
            end: new Date(schedule.schedule),
            color: this.colors[i],
            timed: false
          }
        })

        this.isLoadingItems = false
      } catch (error) {
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/user/login')
        }

        this.alert.isOpen = true
      }
    }
  }
}
</script>
